@import url(https://fonts.googleapis.com/css2?family=Lalezar&family=Source+Sans+Pro:ital,wght@0,400;0,700;1,300&display=swap);
body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}

.wrapper {
  width: 100%;
  height: 100%;
  padding: 5%;
  display: flex;
}

.content {
  width: 50%;
}

.illustration {
  /* display: flex;
  flex-direction: column;
  justify-content: flex-end;
  align-items: center; */

  padding-left: 10%;
  padding-bottom: 1%;
}

.illustration-tile {
  width: 220px;
  height: 220px;
  border-radius: 10px;
  background-color: #c4c4c4;
  margin-top: 160px;
  align-content: center;
  display: flex;

  justify-content: center;
  align-items: center;
  /* position: absolute; */
}

.illustration-coin {
  width: 220px;
  height: 220px;
  opacity: 0;
  /* margin-top: 160px; */
  /* position: absolute; */
  animation: fly 3s infinite;
}

@keyframes fly {
  0% {
    opacity: 1;
  }
  100% {
    margin-top: -600px;
    opacity: 0;
    width: 80px;
    height: 80px;
  }
}

.page-title {
  font-family: Lalezar;
  font-style: normal;
  font-weight: normal;
  font-size: 48px;
  line-height: 48px;
  color: #000000;
  margin: 0;
}

.main-text {
  font-family: Source Sans Pro;
  font-style: normal;
  font-weight: normal;
  font-size: 18px;
  line-height: 18px;
  color: #000000;
  margin: 0;
}

.bold-text {
  font-family: Source Sans Pro;
  font-style: normal;
  font-weight: bold;
  font-size: 18px;
  line-height: 18px;
  color: #000000;
  margin: 0;
}

.accent-text {
  font-family: Source Sans Pro;
  font-style: italic;
  font-weight: normal;
  font-size: 24px;
  color: #f39c12;
  margin: 0;
}

.small-text {
  font-family: Source Sans Pro;
  font-style: normal;
  font-weight: normal;
  font-size: 12px;
  line-height: 15px;
  color: #000000;
}

.button {
  background: #f39c12;
  border-radius: 10px;
  border: none;
  font-family: Source Sans Pro;
  font-style: normal;
  font-weight: 900;
  font-size: 18px;
  display: flex;
  align-items: center;
  color: #000000;
  padding: 4px 12px 4px 12px;
  height: 36px;
}

.button:hover {
  background-color: #a24936;
  color: white;
}
.raw {
  display: flex;
  grid-gap: 1rem;
  gap: 1rem;
  align-items: center;
}

.section {
  margin-top: 0.5rem;
  margin-bottom: 1rem;
}

.header {
  margin-bottom: 5rem;
}

.footer {
  position: fixed;
  bottom: 0;
  padding-bottom: 2%;
}

.dropdown {
  border: none;
  padding: 2px;
  background-color: white;
  font-family: Source Sans Pro;
  font-style: normal;
  font-weight: bold;
  font-size: 18px;
  line-height: 23px;
  color: #000000;
}

.input {
  border-radius: 3px;
}

input[type="range"] {
  height: 13px;
  -webkit-appearance: none;
  margin: 10px 0;
  width: 80%;
}
input[type="range"]:focus {
  outline: none;
}
input[type="range"]::-webkit-slider-runnable-track {
  width: 100%;
  height: 1px;
  cursor: pointer;
  animate: 0.2s;
  box-shadow: 0px 0px 0px #000000;
  background: #000000;
  border-radius: 0px;
  border: 0px solid #000000;
}
input[type="range"]::-webkit-slider-thumb {
  box-shadow: 0px 0px 0px #000000;
  border: 0px solid #000000;
  height: 7px;
  width: 7px;
  border-radius: 3px;
  background: #000000;
  cursor: pointer;
  -webkit-appearance: none;
  margin-top: -3px;
}
input[type="range"]:focus::-webkit-slider-runnable-track {
  background: #000000;
}
input[type="range"]::-moz-range-track {
  width: 100%;
  height: 1px;
  cursor: pointer;
  animate: 0.2s;
  box-shadow: 0px 0px 0px #000000;
  background: #000000;
  border-radius: 0px;
  border: 0px solid #000000;
}
input[type="range"]::-moz-range-thumb {
  box-shadow: 0px 0px 0px #000000;
  border: 0px solid #000000;
  height: 7px;
  width: 7px;
  border-radius: 3px;
  background: #000000;
  cursor: pointer;
}
input[type="range"]::-ms-track {
  width: 100%;
  height: 1px;
  cursor: pointer;
  animate: 0.2s;
  background: transparent;
  border-color: transparent;
  color: transparent;
}
input[type="range"]::-ms-fill-lower {
  background: #000000;
  border: 0px solid #000000;
  border-radius: 0px;
  box-shadow: 0px 0px 0px #000000;
}
input[type="range"]::-ms-fill-upper {
  background: #000000;
  border: 0px solid #000000;
  border-radius: 0px;
  box-shadow: 0px 0px 0px #000000;
}
input[type="range"]::-ms-thumb {
  margin-top: 1px;
  box-shadow: 0px 0px 0px #000000;
  border: 0px solid #000000;
  height: 7px;
  width: 7px;
  border-radius: 3px;
  background: #000000;
  cursor: pointer;
}
input[type="range"]:focus::-ms-fill-lower {
  background: #000000;
}
input[type="range"]:focus::-ms-fill-upper {
  background: #000000;
}

.school-logo {
  height: 20px;
}

.wrapper-align-center {
  height: 100%;
  padding: 15%;
  display: flex;
  flex-direction: column;
  align-items: center;
}

.stats-coin {
  height: 1rem;
}

.table-data {
  text-align: right;
  padding: 4px 12px 4px 12px;
}

.table-data-time {
  text-align: right;
  display: flex;
  grid-column-gap: 0.5rem;
  -webkit-column-gap: 0.5rem;
          column-gap: 0.5rem;
  padding: 4px 12px 4px 12px;
}

.table-data-headings {
  border-bottom: 1px solid;
}

table {
  border-spacing: 0;
}

.table-body {
  margin-top: 1rem;
}

.game-field-tile {
  width: 40px;
  height: 40px;
  border-radius: 4px;
  background-color: #c4c4c4;
  display: flex;
  justify-content: center;
  align-items: center;
}

.game-field-tile-empty {
  width: 40px;
  height: 40px;
  border-radius: 4px;
  background-color: #c4c4c4;
}

.game-field-tile-clicked {
  width: 40px;
  height: 40px;
  border-radius: 4px;
  background-color: #f3f3f3;
  display: flex;
  justify-content: center;
  align-items: center;
}

.game-field-coin {
  width: 30px;
  height: 30px;
  opacity: 0;
  animation: up 2s;
}

@keyframes up {
  0% {
    opacity: 1;
  }

  100% {
    opacity: 0;
    margin-top: -200px;
  }
}

.whole-tile {
  position: relative;
  display: flex;
  align-items: center;
  justify-content: center;
  width: 40px;
  height: 40px;
}

.game-field {
  display: flex;
  flex-wrap: wrap;
  grid-gap: 0.5rem;
  gap: 0.5rem;
  margin-top: 3rem;
}

.game-wrapper {
  width: 90%;
  height: 90%;
  padding: 5%;
}

.game-top-panel {
  display: flex;
  justify-content: space-between;
  align-items: center;
}

.link {
  text-decoration: none;
}

.congrats-header {
  display: flex;
}

.congrats-raw {
  display: flex;
  grid-gap: 0.25rem;
  gap: 0.25rem;
  align-items: center;
  margin-top: 2rem;
}

.music-controls {
  border: none;
  background-color: white;
}

.music-controls-icon {
  width: 24px;
  height: 24px;
  opacity: 0.8;
}

.music-controls:hover {
  cursor: pointer;
}

.music-controls-icon:hover {
  opacity: 1;
}

.appear {
  animation: soft-appear 2s;
}

@keyframes soft-appear {
  0% {
    opacity: 0;
  }
  100% {
    opacity: 1;
  }
}

@media (max-width: 651px) {
  .illustration {
    display: none;
  }

  .content {
    width: 80%;
    padding: 10%;
  }

  /* .game-top-panel {
    justify-content: space-around;
    gap: 1rem;
  } */
}

.game-panel {
  display: flex;
  justify-content: space-between;
  align-content: center;
  margin-top: 1rem;
}

